.Loader {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%; }
  .Loader .Loader__Overlay {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }
  .Loader .Loader__Wrapper {
    width: 50px;
    height: 50px;
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%); }
  .Loader .Loader__section {
    width: 25px;
    height: 25px;
    -webkit-animation: loading 1s linear infinite;
            animation: loading 1s linear infinite; }
  .Loader .Loader__section:nth-child(1) {
    background: #FFDE00;
    -webkit-animation-delay: 0;
            animation-delay: 0; }
  .Loader .Loader__section:nth-child(2) {
    background: #000;
    -webkit-animation-delay: 0.25s;
            animation-delay: 0.25s; }
  .Loader .Loader__section:nth-child(3) {
    background: #000;
    -webkit-animation-delay: .75s;
            animation-delay: .75s; }
  .Loader .Loader__section:nth-child(4) {
    background: #FFDE00;
    -webkit-animation-delay: .50s;
            animation-delay: .50s; }

@-webkit-keyframes loading {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

@keyframes loading {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

